/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  color: #1a1b15;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  background-color: #e7e8e9;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 2000px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  margin: 0 0 2.5rem 0;
  font-weight: 600;
  color: #1a1b15;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #1a1b15;
  font-size: 1.5rem;
}

.header {
  padding: 1rem 4rem;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #e7e8e9;
  background-color: #FFFFFF;
}
.header .enlaces {
  display: flex;
  align-items: center;
  gap: 3rem;
}
.header .enlaces a {
  color: black;
  transition: transform 0.1s, opacity 0.2s;
}
.header .enlaces a:hover {
  color: #0046b8;
  transform: scale(1.1);
  /* Escala ligeramente el enlace en hover */
  opacity: 0.9;
}
.header .perfil {
  display: flex;
  align-items: center;
  position: relative;
  display: inline-block;
  cursor: pointer;
}

.icono-perfil {
  font-size: 50px;
}

.pg {
  width: 50px;
  height: 50px;
  background-image: url(../img/siemprejuntos.svg);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

.nav2 {
  padding: 0.5rem 0;
  background-color: #FFFFFF;
}
.nav2 .flex {
  display: flex;
  align-items: center;
  gap: 0.4rem;
}
.nav2 p {
  color: #5f6b77;
}

.bienvenida {
  background-color: #FFFFFF;
  padding: 3rem;
  margin: 3rem;
}

.grid {
  display: grid;
  grid-template-columns: 30% 70%;
}

.dropdown-menu {
  display: none;
  position: absolute;
  top: 100%;
  right: 0;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 0.4rem;
}
.dropdown-menu ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
.dropdown-menu li {
  padding: 1.3rem 2.2rem;
}

.perfil:hover .dropdown-menu {
  display: block;
}

.dropdown-menu li a {
  text-decoration: none;
  color: #1a1b15;
}

.dropdown-menu li:hover {
  background-color: rgba(244, 244, 244, 0.9);
}
.dropdown-menu li:hover a {
  color: #0046b8;
}

.tabla .nav-tabla {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #e7e8e9;
}
.tabla .nav-tabla .tabla-botones {
  display: flex;
  gap: 1rem;
}
.tabla .boton {
  background: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  border: 1px solid #e7e8e9;
  border-radius: 0.5rem;
  height: 35px;
  display: flex;
  align-items: center;
  margin-bottom: 1rem;
}
.tabla .boton:active {
  transform: scale(1.05);
}

.tabla-principal {
  font-size: 1.4rem;
  border-collapse: collapse;
  width: 100%;
  margin-bottom: 20px;
}
.tabla-principal th,
.tabla-principal td {
  border-bottom: 1px solid #dddddd;
  padding: 8px;
  text-align: left;
}
.tabla-principal tr:last-child {
  border-bottom: 2px solid #dddddd;
}
.tabla-principal thead th {
  font-weight: 600;
  color: #5f6b77;
}

.modal {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  overflow-y: scroll;
}
.modal .modal-contenido {
  background-color: white;
  max-width: 1200px;
  margin: 5% auto;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
  border-radius: 0.3rem;
}
.modal .cerrar-modal {
  font-size: 24px;
  cursor: pointer;
}
.modal .title {
  border-bottom: 1px solid #e7e8e9;
  padding: 1rem 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.modal .form-body {
  padding: 2rem 4rem;
}

.form-section1 {
  display: inline-block;
  margin: 5px;
}
.form-section1 .block {
  display: block;
}

.navModal {
  padding: 1rem 4rem;
  display: flex;
  gap: 2rem;
}

.navModal a {
  color: #5f6b77;
  text-decoration: none;
  padding: 5px;
  transition: color 0.3s;
  border: 1px solid white;
}

.navModal a:hover {
  border: 1px solid #007bff;
  border-radius: 14px;
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
}

.navModal a.active,
.navModal a:focus,
.navModal a:active {
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
  border: 1px solid #007bff;
  border-radius: 14px;
}

/* Cambia el color del borde cuando se enfoca */
/* Estilos para el botón de guardar */
#guardarBtn {
  margin-top: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
}

/* Cambio de estilo al pasar el cursor sobre el área de arrastre */
.drop-area:hover {
  background-color: #e0e0e0;
}

#cliente {
  position: relative;
  /* Asegura que el div de sugerencias esté posicionado correctamente */
}

#suggestions {
  position: absolute;
  display: none;
  background-color: white;
  border: 1px solid #ccc;
  max-height: 200px;
  z-index: 1000;
  width: 270px;
  overflow-y: auto;
  cursor: pointer;
  border-radius: 4px;
  /* Asegura que el div de sugerencias tenga el mismo ancho que el input */
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  /* Agrega una sombra suave */
}

.suggestions > div {
  /* Establece un borde inferior para cada sugerencia, excepto la última */
  border-bottom: 1px solid #ccc;
  /* Puedes ajustar el estilo y color del borde */
  padding: 7px 12px;
  /* Agrega un espacio interno para las sugerencias */
}
.suggestions > div:hover {
  background-color: #e6e6e6;
  border-radius: 2px;
}
.suggestions > div:last-child {
  border-bottom: none;
}

#drop-area {
  width: 300px;
  height: 200px;
  border: 2px dashed #ccc;
  text-align: center;
  padding: 20px;
  cursor: pointer;
}

h2 {
  text-align: center;
  margin-bottom: 20px;
}

label {
  font-weight: bold;
  display: block;
  margin-bottom: 10px;
}

input[type=file] {
  width: 100%;
