select {
  padding: 5px;
  /* Espaciado interno */
  border: 1px solid #ccc;
  /* Borde */
  border-radius: 5px;
  /* Bordes redondeados */
  width: 100%;
  /* Ancho completo */
  cursor: pointer;
}

/* Estilo para la opción predeterminada (placeholder) */
select option[disabled]:first-child {
  color: #aaa;
  /* Color de texto gris */
}

/* Estilo para las opciones normales */
select option {
  color: #333;
  /* Color de texto normal */
  background-color: #fff;
  /* Fondo blanco */
}

/* Estilo cuando se enfoca (activo) */
select:focus {
  border-color: #007bff;
  /* Cambia el color del borde cuando se enfoca */
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
  /* Sombra suave cuando se enfoca */
}

textarea {
  margin: 2rem auto;
  display: block;
  width: 90%;
  /* Ancho completo */
  padding: 10px;
  /* Espaciado interno */
  border: 1px solid #ccc;
  /* Borde */
  border-radius: 5px;
  /* Bordes redondeados */
  resize: vertical;
  /* Permite redimensionar verticalmente */
}

/* Estilos cuando el textarea está enfocado (activo) */
textarea:focus {
  border-color: #007bff;
  /* Cambia el color del borde cuando se enfoca */
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
  /* Sombra suave cuando se enfoca */
}

